div.Grid {
    align-content: center;
}

div.RowContainer {
    width: 32vw;
}

div.Row {
    margin: 0;
    display: flex;
    flex-direction: row;
    width: 100%;
}

div.Number {
    border-style: solid;
    width: 8vw;
}

div.Number h2 {
    vertical-align: middle;
    text-align: center;
}

div.ResultTextContainer {
    width: 32vw;
    vertical-align: middle;
    text-align: right;
    align-content: right;
    display: flex;
    flex-direction: row;
}

div.ResultText {
    display: table-cell;
    font-size: 6vh;
    padding-right: 1vw;
    border-style: solid;
    width: 73%;
    overflow: hidden;
    text-overflow: ellipsis;
}